import React, { Component } from 'react';
import '../App.scss';
import { withStyles, createStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import PictureAsPdfIcon from '@material-ui/icons/PictureAsPdf';
import WebIcon from '@material-ui/icons/Web';
import OndemandVideoIcon from '@material-ui/icons/OndemandVideo';

const styles = createStyles({
  subTitle: {
    fontSize: '30px',
    fontWeight: 800,
    color: '#555555',
    textDecoration: 'none',
  },
  contentText1: {
    fontSize: '16px',
    // paddingRight: '40px',
  },
  contentText2: {
    fontSize: '18px'
  },
  educationLogo: {
    width: '100%',
  },
  contentNode: {
    margin: '0 0 0px 0'
  },
  publicationTeaserContainer: {
    width: '100%',
    height: '140px',
    borderRadius: '4px',
    boxShadow: '#00000024 0px 0px 4px 2px',
  },
  publicationTeaser: {
    width: '100%',
    height: '100%',
    objectFit: "cover",
  },
  publicationTitle: {
    fontWeight: 800,
    fontSize: '20px',
    color: '#353535',
  },
  publicationAuthors: {
    color: '#555555',
  },
  publicationJournal: {
    color: '#555555',
    fontStyle: 'italic',
  },
  publicationIconText: {
    fontSize: '18px',
    margin: '0 20px 0 0',
    position: "relative",
    top: '-6px',
  },
});

type Props = {
  classes: {
    [propName: string]: string
  }
}

class Publication extends Component<Props> {
  state = {
    publications: [
      {
        title: 'Narrative Transitions in Data Videos',
        journal: '',
        conference: 'IEEE VIS 2020 Short Paper',
        author: 'Junxiu Tang, Lingyun Yu, Tan Tang, Xinhuan Shu, Lu Ying, Yuhua Zhou, Peiran Ren, Yingcai Wu',
        teaser: './publication/VIS20-transition.png',
        url: 'https://arxiv.org/abs/2009.05233',
        website: 'https://narrativetransitions.github.io/home/',
        video: 'https://www.youtube.com/watch?v=RNFCuR9DgMg'
      },
      {
        title: 'What Makes a Data-GIF Understandable?',
        journal: 'IEEE Transactions on Visualization and Computer Graphics',
        conference: 'IEEE InfoVis 2020',
        author: 'Xinhuan Shu, Aoyu Wu, Junxiu Tang, Benjamin Bach, Yingcai Wu, and Huamin Qu',
        teaser: './publication/VIS20-datagif.png',
        url: 'https://arxiv.org/abs/2008.07227',
        website: 'https://data-gifs.github.io/',
        video: 'https://www.youtube.com/watch?v=vs3iucWl0qI'
      },
      {
        title: 'SmartShots: Enabling Automatic Generation of Videos with Data Visualizations Embedded',
        journal: 'Proceedings of the 28th ACM International Conference on Multimedia',
        conference: 'ACM MM 2020 ',
        author: 'Tan Tang, Junxiu Tang, Jiewen Lai, Lu Ying, Peiran Ren, Lingyun Yu, Yingcai Wu',
        teaser: './publication/mm20-smartshots.png',
        url: 'https://dl.acm.org/doi/10.1145/3394171.3414356',
        website: '',
        video: ''
      },
      {
        title: 'Design Guidelines for Augmenting Short-form Videos using Animated Data Visualizations',
        journal: 'Journal of Visualization',
        conference: 'China Vis 2019',
        author: 'Tan Tang, Junxiu Tang, Jiayi Hong, Lingyun Yu, Peiran Ren, Yingcai Wu',
        teaser: './publication/jov19-guideline.png',
        url: 'http://link.springer.com/article/10.1007/s12650-020-00644-z',
        website: '',
        video: ''
      },
      {
        title: 'Toward the Better Modeling and Visualization of Uncertainty for Streaming Data',
        journal: 'Journal of Visualization',
        conference: 'China Vis 2018',
        author: 'Tan Tang, Kaijuan Yuan, Junxiu Tang, Yingcai Wu',
        teaser: './publication/jov18-uncertainty.png',
        url: 'https://link.springer.com/article/10.1007/s12650-018-0518-y',
        website: '',
        video: ''
      },
    ]
  }

  render() {
    const { classes } = this.props;
    // const { publications } = this.state.publications;
    const publicationNode = 
      <React.Fragment>
        <Grid container spacing={2}>
          <Grid item md={8}>
            <Typography variant="h2" component="h2" gutterBottom align="left">
              <a className={classes.subTitle} href='#publication'>{`# Publication`}</a>
            </Typography>
          </Grid>
        </Grid>
        {this.state.publications.map((pub, i) => (
          <Grid container spacing={4} justify="center" alignItems="center" className={classes.contentNode}>
            <Grid item md={3}>
              <div className={classes.publicationTeaserContainer}>
                <img alt={pub.title} src={pub.teaser} className={classes.publicationTeaser}/>
              </div>
            </Grid>
            <Grid item md={9}>
              <Typography variant="subtitle1" gutterBottom align="left" className={classes.publicationTitle}>
                {pub.title}
              </Typography>
              <Typography variant="subtitle1" gutterBottom align="left" className={classes.publicationJournal}>
                {pub.journal} {pub.conference === "" ? null : ` (${pub.conference})`}
              </Typography>
              <Typography variant="subtitle1" gutterBottom align="left" className={classes.publicationAuthors}>
                {pub.author}
              </Typography>
              <Typography variant="subtitle1" gutterBottom align="left" className={classes.publicationAuthors}>
                <Grid item md={6}>
                  {pub.url === "" ? null : (<React.Fragment><a href={pub.url}><PictureAsPdfIcon /> <span className={classes.publicationIconText}>pdf</span></a></React.Fragment>)}
                  {pub.website === "" ? null : (<React.Fragment><a href={pub.website}><WebIcon /> <span className={classes.publicationIconText}>website</span></a></React.Fragment>)}
                  {pub.video === "" ? null : (<React.Fragment><a href={pub.video}><OndemandVideoIcon /> <span className={classes.publicationIconText}>video</span></a></React.Fragment>)}
                </Grid>
              </Typography>
            </Grid>
          </Grid>
        ))}
      </React.Fragment>
    return (
      <div className="publication">
        { publicationNode }
      </div>
    )
  }
}

export default withStyles(styles)(Publication);
