import React, { Component } from 'react';
import '../App.scss';
import { withStyles, createStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';

const styles = createStyles({
  subTitle: {
    fontSize: '30px',
    fontWeight: 800,
    color: '#555555',
    textDecoration: 'none',
  },
  contentText1: {
    fontSize: '16px',
    // paddingRight: '40px',
  },
  contentText2: {
    fontSize: '18px'
  },
  contentText3: {
    fontWeight: 800,
    fontSize: '20px',
    color: '#111111',
  },
  experienceLogo: {
    width: '100%',
  },
  contentNode: {
    margin: '0 0 0px 0'
  }
});

type Props = {
  classes: {
    [propName: string]: string
  }
}

class Experience extends Component<Props> {
  render() {
    const { classes } = this.props;
    const experienceNode = 
      <React.Fragment>
        <Grid container spacing={6}>
          <Grid item md={8}>
            <Typography variant="h2" component="h2" gutterBottom align="left">
              <a className={classes.subTitle} href='#experience'>{`# Experience`}</a>
            </Typography>
          </Grid>
        </Grid>
        <Grid container spacing={4} justify="center" alignItems="center" className={classes.contentNode}>
          <Grid item md={3}>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText1}>
              {`Nov. 2019 - now`}
            </Typography>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText1}>
              {`Hangzhou, China`}
            </Typography>
          </Grid>
          <Grid item md={7}>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText3}>
              {`Research Intern`}
            </Typography>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText2}>
              {`Alibaba Group`}
            </Typography>
          </Grid>
          <Grid item md={2}>
            <img alt="Alibaba Group" src="./logo/alibaba.png" className={classes.experienceLogo}/>
          </Grid>
        </Grid>
        <Grid container spacing={4} justify="center" alignItems="center" className={classes.contentNode}>
          <Grid item md={3}>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText1}>
              {`Nov. 2019 - Sept. 2020`}
            </Typography>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText1}>
              {`Hangzhou, China`}
            </Typography>
          </Grid>
          <Grid item md={7}>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText3}>
              {`Research Intern`}
            </Typography>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText2}>
              {`Alibaba DAMO Academy for Discovery, Adventure, Momentum and Outlook`}
            </Typography>
          </Grid>
          <Grid item md={2}>
            <img alt="Alibaba DAMO Academy for Discovery, Adventure, Momentum and Outlook" src="./logo/damo.svg" className={classes.experienceLogo}/>
          </Grid>
        </Grid>
        <Grid container spacing={4} justify="center" alignItems="center" className={classes.contentNode}>
          <Grid item md={3}>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText1}>
              {`Sept. 2019 - now`}
            </Typography>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText1}>
              {`Hangzhou, China`}
            </Typography>
          </Grid>
          <Grid item md={7}>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText3}>
              <b>{`Research Intern`}</b>
            </Typography>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText2}>
              {`Alibaba-Zhejiang University Joint Research Institute of Frontier Technologise`}
            </Typography>
          </Grid>
          <Grid item md={2}>
            <img alt="Alibaba-Zhejiang University Joint Research Institute of Frontier Technologise" src="./logo/azft.png" className={classes.experienceLogo}/>
          </Grid>
        </Grid>
      </React.Fragment>
    return (
      <div className="experience">
        { experienceNode }
      </div>
    )
  }
}

export default withStyles(styles)(Experience);
