import React, { Component } from 'react';
import '../App.scss';
import { withStyles, createStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';

const styles = createStyles({
  subTitle: {
    fontSize: '30px',
    fontWeight: 800,
    color: '#555555',
    textDecoration: 'none',
  },
  contentText1: {
    fontSize: '16px',
    // paddingRight: '40px',
  },
  contentText2: {
    fontSize: '18px'
  },
  contentText3: {
    fontWeight: 800,
    fontSize: '20px',
    color: '#111111',
  },
  educationLogo: {
    width: '100%',
  },
  contentNode: {
    margin: '0 0 0px 0'
  }
});

type Props = {
  classes: {
    [propName: string]: string
  }
}

class Education extends Component<Props> {
  render() {
    const { classes } = this.props;
    const educationNode = 
      <React.Fragment>
        <Grid container spacing={6}>
          <Grid item md={8}>
            <Typography variant="h2" component="h2" gutterBottom align="left">
              <a className={classes.subTitle} href='#education'>{`# Education`}</a>
            </Typography>
          </Grid>
        </Grid>
        <Grid container spacing={4} justify="center" alignItems="center" className={classes.contentNode}>
          <Grid item md={3}>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText1}>
              {`Sept. 2019 - now`}
            </Typography>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText1}>
              {`Hangzhou, China`}
            </Typography>
          </Grid>
          <Grid item md={7}>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText3}>
              {`Ph.D Student`}
            </Typography>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText2}>
              {`State Key Lab of CAD&CG, Zhejiang University`}
            </Typography>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText2}>
              {`Supervised by Prof. Yingcai Wu`}
            </Typography>
          </Grid>
          <Grid item md={2}>
            <img alt="Zhejiang University" src="./logo/zju.png" className={classes.educationLogo}/>
          </Grid>
        </Grid>
        <Grid container spacing={4} justify="center" alignItems="center" className={classes.contentNode}>
          <Grid item md={3}>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText1}>
              {`Sept. 2014 - Jun. 2019`}
            </Typography>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText1}>
              {`Hangzhou, China`}
            </Typography>
          </Grid>
          <Grid item md={7}>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText3}>
              <b>{`Undergraduate Student`}</b>
            </Typography>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText2}>
              {`College of Computer Science and Technology, Zhejiang University`}
            </Typography>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText2}>
              {`Bachelor in Digital Media Technology`}
            </Typography>
          </Grid>
          <Grid item md={2}>
            <img alt="Zhejiang University" src="./logo/zju.png" className={classes.educationLogo}/>
          </Grid>
        </Grid>
      </React.Fragment>
    return (
      <div className="education">
        { educationNode }
      </div>
    )
  }
}

export default withStyles(styles)(Education);
