import React, { Component } from 'react';
import '../App.scss';
import { withStyles, createStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import VerticalTabs from './VerticalTabs';
import Publication from './Publication';
import Education from './Education';
import Experience from './Experience';

const styles = createStyles({
  subTitle: {
    fontSize: '30px',
    fontWeight: 800,
    color: '#555555',
    textDecoration: 'none',
  },
  contentText1: {
    fontSize: '16px',
    // paddingRight: '40px',
  },
  contentText2: {
    fontSize: '18px'
  },
  educationLogo: {
    width: '100%',
  },
  contentNode: {
    margin: '0 0 0px 0'
  },
  publicationTeaserContainer: {
    width: '100%',
    height: '140px',
    borderRadius: '4px',
    boxShadow: '#00000024 0px 0px 4px 2px',
  },
  publicationTeaser: {
    width: '100%',
    height: '100%',
    objectFit: "cover",
  },
  publicationTitle: {
    fontWeight: 800,
    fontSize: '20px',
    color: '#353535',
  },
  publicationAuthors: {
    color: '#555555',
  },
  publicationIconText: {
    fontSize: '18px',
    margin: '0 5px',
    position: "relative",
    top: '-6px',
  }
});

type Props = {
  classes: {
    [propName: string]: string
  }
}

class Content extends Component<Props> {
  render() {
    const { classes } = this.props;
    const contactNode = 
      <React.Fragment>
        <Grid container spacing={6}>
          <Grid item md={8}>
            <Typography variant="h2" component="h2" gutterBottom align="left">
              <a className={classes.subTitle} href='#contact'>{`# Contact`}</a>
            </Typography>
          </Grid>
        </Grid>
        <Grid container spacing={8} justify="center" alignItems="center" className={classes.contentNode}>
          <Grid item md={3} className={classes.contentText}>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText1}>
              {`Address`}
            </Typography>
          </Grid>
          <Grid item md={9} className={classes.contentText}>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText2}>
              {`Rm.408-2, Mengminwei Bd., Zijingang Campus of Zhejiang University`}
              <br/>
              {`No.866 Yuhangtang Rd., Xihu District, Hangzhou, Zhejiang. 310058`}
            </Typography>
          </Grid>
        </Grid>
        <Grid container spacing={8} justify="center" alignItems="center" className={classes.contentNode}>
          <Grid item md={3} className={classes.contentText}>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText1}>
              {`E-mail`}
            </Typography>
          </Grid>
          <Grid item md={9} className={classes.contentText}>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText2}>
              {`tangjunxiu@zju.edu.cn`}
            </Typography>
          </Grid>
        </Grid>
      
      </React.Fragment>
    
    return (
      <div className="content">
        <Education />
        <Experience />
        <Publication />
        { contactNode }
      </div>
    )
  }
}

export default withStyles(styles)(Content);
